<template>
  <div>
    <div id="sec1" data-aos="fade-up" data-aos-once="true">
    <div>
    <h5 style="float:left">대기 중인 신청</h5>
    </div><br><br>
    <div v-if="waitingList.length === 0">
    <CCard class="light_shadow">
      <CCardBody @click="gotoLogin()">
        <CRow>
            <CCol style="width:80%">
                <h4>대기 중인 신청이 없습니다.</h4>
            </CCol>
        </CRow>
      </CCardBody>
    </CCard>
    </div>
    <div v-else>
    <CCard
        class="light_shadow"
        v-for="(cards, index) in waitingList" 
        :key="index"
    >
      <CCardBody>
        <CRow>
            <CCol style="width:80%">
                <h4>{{cards.title}}</h4>
                <h6>카테고리: {{cards.category}}</h6>
            </CCol>
        </CRow>
        <CRow class="justify-content-between">
            <CCol style="text-align:center">
                <CButton color="danger" style="width: 100px">
                    거부
                </CButton>
            </CCol>
            <CCol style="text-align:center">
                <CButton color="success" style="width: 100px">
                    허가
                </CButton>
            </CCol>
        </CRow>
      </CCardBody>
    </CCard>
    </div>
    </div>

    <div id="sec1" data-aos="fade-up" data-aos-once="true" data-aos-delay="50">
    <div>
    <h5 style="float:left">확정된 신청</h5>
    </div><br><br>
    <div v-if="determinedList.length === 0">
        <CCard class="light_shadow">
      <CCardBody @click="gotoLogin()">
        <CRow>
            <CCol style="width:80%">
                <h4>신청 내역이 없습니다.</h4>
            </CCol>
        </CRow>
      </CCardBody>
    </CCard>
    </div>
    <div v-else>
    <CCard
        class="light_shadow"
        v-for="(cards, index) in determinedList" 
        :key="index"
    >
      <CCardBody @click="gotoLogin()">
        <CRow>
            <CCol style="width:80%">
                <h4>{{cards.title}}</h4>
                <h6>카테고리: {{cards.category}}</h6>
            </CCol>
        </CRow>
      </CCardBody>
      <CCardFooter v-if="cards.state" class="bg-danger disable_padding">
        <h6 id="state_info_denied">신청 거부됨</h6>
      </CCardFooter>
      <CCardFooter v-else class="bg-success disable_padding">
        <h6 id="state_info_accepted">신청 승인됨</h6>
      </CCardFooter>
    </CCard>
    </div>
    </div>
  </div>
</template>
<style scoped>
#state_info_denied{
  text-align: center; margin: 0 auto; color: white; font-weight: 600;
}
#state_info_accepted{
  text-align: center; margin: 0 auto; color: white; font-weight: 600;
}
</style>

<script>
export default {
  name: 'MyRecvList',
  created(){
      this.$store.commit("set", ["pageInfo", "받은 신청 목록"])
      this.$store.commit("set", ["pageRoute", ""])
  },
  data () {
    return {
      waitingList:[
        {
          title: "캡스톤 같이 할 사람",
          category: "나의 학과"
        }
      ],
      determinedList:[
        {
          title: "친구할 사람",
          category: "친구",
          state: false,
        },
        {
          title: "놀러 갈 사람",
          category: "친구",
          state: true,
        }
      ],
      myMajorSample: {
        title: "캡스톤 디자인 팀 구해요~!",
        sub_title: "스마트 앱 개발 계획인데 같이 하실 분!"
      },
      myUnivSample: {
        title: "같이 밥 먹을 사람!",
        sub_title: "배고파아아아아"
      },
      friendsSample: {
        title: "나랑 친구해 줄 사람",
        sub_title: "친구가 없어"
      },
      clubSample: {
        title: "봉사 동아리 인원 모집 중입니다",
        sub_title: "꿀 동아리 보장"
      },
      myAccount: {
          nickname: "닉네임",
          id: "아이디",
      },
    }
  },
  methods: {
    delay_value(index){
      if (index < 4)
        return index * 100
      else return 0
    },
    color (value, max) {
      let $color
      let $temp = value/max
      if ($temp <= 0.5) {
        $color = 'rgb(37,186,14)'
      } else if ($temp > 0.5 && $temp <= 0.75) {
        $color = 'rgb(255, 127, 0)'
      } else if ($temp > 0.75 && $temp < 0.98) {
        $color = 'rgb(245,89,25)'
      }
      else $color = 'rgb(177,30,49)'
      return $color
    },
    gotoLogin(){
        this.$router.push({ path: '/pages/login' })
    },
  }
}
</script>